.App {
  text-align: center;
  background-color: #111111;
  min-height: 100vh;
}

.App-header {
  background-color: rgba(24, 24, 24, 0.95);
  min-height: 5vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
  color: white;
  margin: 0 auto;
  position: fixed;
  width: 100%;
}

h2.header-text {
  letter-spacing: 10px;
  font-size: calc(26px + 2vmin);
  margin: 25px;
  font-family: "Arial";
}

.content {
  margin: 0 auto;
  background-color: #111111;
  background-size: 100%;
  min-height: 50vh;
  width: 100%;
  display: flex;
  flex-direction: column;
  color: white;
}

.form-div {
    margin: 0 auto;
    background-color: #272727;
    background-size: 100%;
    min-height: 50vh;
    width: 100%;
    display: flex;
    flex-direction: column;
    color: white;
  }

.img-content {
  background-image: url("https://images.unsplash.com/photo-1461696114087-397271a7aedc");
  background-size: 100%;
  background-repeat: no-repeat;
  min-height: 100vh;
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  color: whitesmoke;
}

.btnAdd {
  width: 150px;
  height: 30px;
}

.cart-btn {
  width: 250px;
  height: 50px;
  margin: 0 auto;
  margin-top: 100px;
}

.order-btn {
    width: 250px;
    height: 50px;
    margin: 0 auto;
    margin-top: 50px;
}

.cart-div {
  margin: 0 auto;
  padding: 50px;
  flex-direction: column;
  display: flex;
  width: 50%;
  font-size: 2vmin;
}

.summary-div {
    background-color: #111111;
    margin: 0 auto;
    width: 100%;
    color: white;
    padding-bottom: 100px;
}

.form-div {
    background-color: #111111;
    margin: 0 auto;
    width: 100%;
    color: white;
    padding-bottom: 100px;
}

.cart-th {
  width: 100px;
}

.img-overlay {
  background-color: rgba(24, 24, 24, 0.5);
  min-height: 100vh;
  width: 100%;
}

.default-div {
  padding-top: 25px;
  padding-bottom: 25px;
}

.default-div .img-portrait {
  width: 75%;
}

.product-div {
  padding-top: 25vh;
  display: flex;
}

.default-div .product-img {
  width: 250px;
}

.product-container {
  margin: auto;
  border: 2px solid black;
  width: 300px;
  min-height: 450px;
  background-color: #111111;
  color: white;
  font-size: 16pt;
  font-weight: bold;
  padding-bottom: 10px;
}

.img-container {
  padding-top: 30px;
  width: 300px;
  height: 275px;
  border-bottom: 2px solid black;
  background-color: white;
}

input {
    width: 250px;
    height: 20px;
    margin-bottom: 20px;
}